import React from 'react';
import '../styles/Books.css';
import ScrollToTopButton from '../components/ScrollTopButton';

const PythonLinks = () => {
  const resources = [
    {
      title: "  Python",
      link: "https://docs.python.org/3/",
      description: "  Python      ,  ,     ."
    },
    {
      title: " '  Python'  Coursera",
      link: "https://www.coursera.org/specializations/python",
      description: "  Coursera     Python,   ,  ,     ."
    },
    {
      title: " ' Python,  ,    '  Ozon",
      link: "https://www.ozon.ru/context/detail/id/141906899/",
      description: "      Python,   ,    ."
    },
    {
      title: " 'Python  '  YouTube",
      link: "https://www.youtube.com/playlist?list=PLlb7e2G7aSpQrZIIAY8s6lrX7vlbY2tBO",
      description: "  YouTube      Python,  , , ,    ."
    },
    {
      title: " '    Python'  Stepik",
      link: "https://stepik.org/course/67",
      description: "  Stepik      Python     ."
    }
  ];

  return (
    <div className="books-container ">
      <h1>    Python</h1>
      <ul className="books-list">
        {resources.map((resource, index) => (
          <li key={index} className="book-item">
            <h2><a href={resource.link} target="_blank" rel="noopener noreferrer">{resource.title}</a></h2>
            <p>{resource.description}</p>
          </li>
        ))}
      </ul>
      <ScrollToTopButton /> 
    </div>
  );
}

export default PythonLinks;