import React from 'react';
import '../styles/Lesson-style.css';
import CodeExecutor from '../components/CodeExecutor';
import ScrollToTopButton from '../components/ScrollTopButton';

const OperatorsAndExpressions = () => {
  return (
    <div className="lesson-container">
      <div className="lesson-header">
        <h1>  </h1>
      </div>

      <div className="lesson-section">
        <h2> </h2>
        <p>
                 :
        </p>
        <ul>
          <li><code>+</code> - </li>
          <li><code>-</code> - </li>
          <li><code>*</code> - </li>
          <li><code>/</code> - </li>
          <li><code>%</code> -    (       ) </li> 
          <li><code>**</code> -   </li>
          <li><code>{'//'}</code> -   (   ,  
              )</li> 
        </ul>
        <pre>
          <code>
            {`#    \n`}
            {`a = 10\n`}
            {`b = 3\n`}
            {`print(a + b)  # : 13\n`}
            {`print(a - b)  # : 7\n`}
            {`print(a * b)  # : 30\n`}
            {`print(a / b)  # : 3.3333333333333335\n`}
            {`print(a % b)  # : 1\n`}
            {`print(a ** b) # : 1000\n`}
            {`print(a // b) # : 3\n`}
          </code>
        </pre>
      </div>

      <div className="lesson-section">
        <h2> </h2>
        <p>
                :
        </p>
        <ul>
          <li><code>and</code> -  </li>
          <li><code>or</code> -  </li>
          <li><code>not</code> -  </li>
        </ul>
        <pre>
          <code>
            {`#    \n`}
            {`x = True\n`}
            {`y = False\n`}
            {`print(x and y)  # : False\n`}
            {`print(x or y)   # : True\n`}
            {`print(not x)    # : False\n`}
          </code>
        </pre>
      </div>

      <div className="lesson-section">
        <h2> </h2>
        <p>
               :
        </p>
        <ul>
          <li><code>==</code> - </li>
          <li><code>!=</code> -  </li>
          <li><code>{'>'}</code> - </li>
          <li><code>&lt;</code> - </li>
          <li><code>{'>='}</code> -   </li>
          <li><code>&lt;=</code> -   </li>
        </ul>
        <pre>
          <code>
            {`#    \n`}
            {`a = 5\n`}
            {`b = 10\n`}
            {`print(a == b)  # : False\n`}
            {`print(a != b)  # : True\n`}
            {`print(a > b)   # : False\n`}
            {`print(a < b)   # : True\n`}
            {`print(a >= b)  # : False\n`}
            {`print(a <= b)  # : True\n`}
          </code>
        </pre>
        <p>       -   ,   .</p>
      </div>

      <div className="lesson-section">
          <h2> </h2>
          <p>
                  :
          </p>
          <ul>
            <li><code>=</code> - </li>
            <li><code>+=</code> -   </li>
            <li><code>-=</code> -   </li>
            <li><code>*=</code> -   </li>
            <li><code>/=</code> -   </li>
            <li><code>%=</code> -     </li>
            <li><code>**=</code> -     </li>
            <li><code>{'//='}</code> -    </li>
          </ul>
          <pre>
            <code>
              {`#    \n`}
              {`a = 5\n`}
              {`a += 2  #  a = a + 2\n`}
              {`print(a)  # : 7\n`}
              {`a -= 1  #  a = a - 1\n`}
              {`print(a)  # : 6\n`}
              {`a *= 3  #  a = a * 3\n`}
              {`print(a)  # : 18\n`}
              {`a /= 2  #  a = a / 2\n`}
              {`print(a)  # : 9.0\n`}
              {`a %= 4  #  a = a % 4\n`}
              {`print(a)  # : 1.0\n`}
              {`a **= 3  #  a = a ** 3\n`}
              {`print(a)  # : 1.0\n`}
              {`a //= 2  #  a = a // 2\n`}
              {`print(a)  # : 0.0\n`}
            </code>
          </pre>
      </div>

      <div className="lesson-section">
        <h2></h2>
        <p>
              Python          
             .        
               .
        </p>
      </div>
      <ScrollToTopButton /> 
    </div>
);
};

export default OperatorsAndExpressions;
