import React from 'react';
import ScrollToTopButton from '../components/ScrollTopButton';

const Cycles = () => {
  return (
    <div className="lesson-container">
      <header className="lesson-header">
        <h1></h1>
      </header>

      <section className="lesson-section">
        <h2> while</h2>
        <p> <code>while</code>      ,   .    
        ,     . :</p>
        <pre>
          <code>
            {`count = 0\n`}
            {`while count < 5:\n`}
            {`    print(count)\n`}
            {`    count += 1\n`}
            {`# :\n`}
            {`# 0\n`}
            {`# 1\n`}
            {`# 2\n`}
            {`# 3\n`}
            {`# 4\n`}
          </code>
          </pre>
          <p>   ,    <code>while</code>     , 
             .</p>
        </section>

        <section className="lesson-section">
          <h2> for</h2>
          <p> <code>for</code>        .  Python    
             <code>range</code>,    . :</p>
          <pre>
            <code>
              {`for i in range(5):\n`}
              {`    print(i)\n`}
              {`# :\n`}
              {`# 0\n`}
              {`# 1\n`}
              {`# 2\n`}
              {`# 3\n`}
              {`# 4\n`}
            </code>
          </pre>
          <p> <code>range</code>    ( ,    ),  
           (   )    ( ,    ). :</p>
          <pre>
            <code>
              {`for i in range(1, 10, 2):\n`}
              {`    print(i)\n`}
              {`# :\n`}
              {`# 1\n`}
              {`# 3\n`}
              {`# 5\n`}
              {`# 7\n`}
              {`# 9\n`}
            </code>
          </pre>
        </section>

        <section className="lesson-section">
          <h2> </h2>
          <p>       .       
             ,    . :</p>
          <pre>
            <code>
              {`for i in range(3):\n`}
              {`    for j in range(2):\n`}
              {`        print("i =", i, "j =", j)\n`}
              {`# :\n`}
              {`# i = 0 j = 0\n`}
              {`# i = 0 j = 1\n`}
              {`# i = 1 j = 0\n`}
              {`# i = 1 j = 1\n`}
              {`# i = 2 j = 0\n`}
              {`# i = 2 j = 1\n`}
            </code>
          </pre>
          <p>            .</p>
        </section>

        <section className="lesson-section">
          <h2> break, continue, pass</h2>
          <p> <code>break</code>   ,     . 
                  . :</p>
          <pre>
            <code>
              {`for i in range(5):\n`}
              {`if i == 3:\n`}
              {`    break\n`}
              {`print(i)\n`}
              {`# :\n`}
              {`# 0\n`}
              {`# 1\n`}
              {`# 2\n`}
            </code>
          </pre>
          <p> <code>continue</code>         
            .        . :</p>
          <pre>
            <code>
              {`for i in range(5):\n`}
              {`if i == 3:\n`}
              {`    continue\n`}
              {`print(i)\n`}
              {`# :\n`}
              {`# 0\n`}
              {`# 1\n`}
              {`# 2\n`}
              {`# 4\n`}
            </code>
          </pre>
          <p> <code>pass</code>       .  ,  
             ,     . :</p>
          <pre>
            <code>
              {`for i in range(5):\n`}
              {`if i == 3:\n`}
              {`    pass\n`}
              {`print(i)\n`}
              {`# :\n`}
              {`# 0\n`}
              {`# 1\n`}
              {`# 2\n`}
              {`# 3\n`}
              {`# 4\n`}
            </code>
          </pre>
        </section>

        <section className="lesson-section">
          <h2></h2>
          <p>       Python,   
             .       <code>while</code>,  <code>for</code>,
              ,    <code>break</code>, <code>continue</code>  <code>pass</code>. 
                       . 
               ,          !</p>
        </section>
        <ScrollToTopButton /> 
    </div>
  );
};

export default Cycles;
