import React from 'react';
import ScrollToTopButton from '../components/ScrollTopButton';

const Libraries = () => {
  return (
    <div className="lesson-container">
      <div className="lesson-header">
        <h1>  </h1>
      </div>

      <div className="lesson-section">
        <h2></h2>
        <p>
           Python   ,    , 
                .     
            : "math", "datetime"  "random".     
             "import"     .
        </p>
      </div>

      <div className="lesson-section">
        <h2> math</h2>
        <p>
           "math"     ,    
           , ,      .
        </p>
        <h3> :</h3>
        <strong>  :</strong>
        <pre>
          <code>
              {`import math\n\n`}

              {`number = 16\n`}
              {`square_root = math.sqrt(number)\n`}
              {`print(square_root)  # : 4.0\n`}
          </code>
        </pre>
        <strong>  :</strong>
        <pre>
          <code>
              {`import math\n\n`}

              {`number = 4.3\n`}
              {`rounded_up = math.ceil(number)\n`}
              {`print(rounded_up)  # : 5\n`}
          </code>
        </pre>
      </div>

      <div className="lesson-section">
        <h2> datetime</h2>
        <p>
           "datetime"          , 
           ,      .
        </p>
        <h3> :</h3>
        <strong>    :</strong>
        <pre>
          <code>
            {`import datetime\n\n`}

            {`now = datetime.datetime.now()\n`}
            {`print(now)  # :    \n`}
          </code>
        </pre>
        <strong>   :</strong>
        <pre>
          <code>
            {`import datetime\n\n`}

            {`now = datetime.datetime.now()\n`}
            {`formatted_date = now.strftime("%Y-%m-%d %H:%M:%S")\n`}
            {`print(formatted_date)  # :      \n`}
          </code>
        </pre>
      </div>

      <div className="lesson-section">
        <h2> random</h2>
        <p>
           "random"       ,  
            ,        
          .
        </p>
        <h3> :</h3>
        <strong>    :</strong>
        <pre>
          <code>
            {`import random\n\n`}

            {`random_number = random.randint(1, 10)\n`}
            {`print(random_number)  # :    1  10\n`}
          </code>
        </pre>
        <strong>  :</strong>
        <pre>
          <code>
            {`import random\n\n`}

            {`my_list = [1, 2, 3, 4, 5]\n`}
            {`random.shuffle(my_list)\n`}
            {`print(my_list)  # :      \n`}
          </code>
        </pre>
      </div>

      <div className="lesson-section">
        <h2></h2>
        <p>        "math", "datetime"  "random"  Python. 
                ,   , 
          ,    .</p>
      </div>
      <ScrollToTopButton /> 
    </div>
  );
};

export default Libraries;
