import React from 'react';
import '../styles/Books.css';
import ScrollToTopButton from '../components/ScrollTopButton';

const Books = () => {
  const books = [
    {
      title: " Python,  ,    ",
      author: " ",
      link: "https://www.ozon.ru/context/detail/id/141906899/",
      description: "    ,      ,     Python  ."
    },
    {
      title: "Python.   ",
      author: " ",
      link: "https://www.ozon.ru/context/detail/id/35146968/",
      description: "     Python,   , ,     ."
    },
    {
      title: "Python   :      ",
      author: " ",
      link: "https://www.ozon.ru/context/detail/id/155303293/",
      description: "   ,    Python            ."
    },
    {
      title: "     Python",
      author: " ",
      link: "https://www.ozon.ru/context/detail/id/169633741/",
      description: "       Python,    ,  , -   ."
    }
];

return (
    <div className="books-container">
      <h1>    Python</h1>
      <ul className="books-list">
        {books.map((book, index) => (
          <li key={index} className="book-item">
            <h2><a href={book.link} target="_blank" rel="noopener noreferrer">{book.title}</a></h2>
            <p><strong>:</strong> {book.author}</p>
            <p>{book.description}</p>
          </li>
        ))}
      </ul>
      <ScrollToTopButton /> 
    </div>
  );
}

export default Books;