import React from 'react';
import '../styles/Lesson-style.css';
import CodeExecutor from '../components/CodeExecutor';
import ScrollToTopButton from '../components/ScrollTopButton';

const InputOutput = () => {
  return (
    <div className="lesson-container">
      <div className="lesson-header">
        <h1>   </h1>
      </div>
      <div className="lesson-section">
        <h2>     </h2>
        <p>
                  ,     . 
           Python            . 
                    .
        </p>
      </div>

      <div className="lesson-section">
        <h2> </h2>
        <p>
              Python   <code>print()</code>.       
             .         ,   .
            ,    .
        </p>
        <pre> 
          <code>
            {`#   print()\n`}
            {`print("Hello, world!")\n`}
            {`age = 25\n`}
            {`print(":", age)\n`}
            {`sum = 5 + 3\n`}
            {`print(" 5  3 =", sum)\n`}
          </code>
        </pre>
        <p>:</p>
        <pre>
          <code>
            {`Hello, world!\n`}
            {`: 25\n`}
            {` 5  3 = 8\n`}
          </code>
        </pre>
        <p>
              <code>print()</code>    "Hello, world!",   <code>age</code>  
            <code>5 + 3</code>.
        </p>
      </div>

      <div className="lesson-section">
        <h2> </h2>
        <p>
              Python   <code>input()</code>.        
          ,         . <code>input()</code>   
               (prompt),     .    
           ,     .
        </p>
        <pre>
          <code>
            {`#   input()\n`}
            {`name = input()\n`}
            {`print(",", name)\n\n`}

            {`age = input()\n`}
            {`print(" :", age)\n`}
          </code>
        </pre>
        <p>    "Alice"   "25":</p>
        <pre>
          <code>
            {` : Alice\n`}
            {`, Alice\n\n`}

            {` : 25\n`}
            {` : 25\n`}
          </code>
        </pre>
        <p>
                        <code>input()</code>     .
        </p>
      </div>

      <div className="lesson-section">
        <h2>    </h2>
        <p>
          ,    <code>input()</code>,    .      ,            <code>int()</code>  <code>float()</code>.
        </p>
        <pre>
          <code>
            {`#      \n`}
            {`age = input("  : ")\n`}
            {`age = int(age)\n`}
            {`print("    :", age + 1)\n\n`}

            {`height = input("    : ")\n`}
            {`height = float(height)\n`}
            {`print(" :", height, "")\n`}
          </code>
        </pre>
        <p>    "25"   "1.75":</p>
        <pre>
          <code>
            {` : 25\n`}
            {`    : 26\n\n`}
            
            {` : 1.75\n`}
            {` : 1.75 \n`}
          </code>
        </pre>
        <p>
                       ,    
             .
        </p>
      </div>

      <div className="lesson-section">
        <h2></h2>
        <p>
                      Python.   
           <code>print()</code>  <code>input()</code>    ,     
              .         
              .
        </p>
      </div>
      {/* <CodeExecutor file = "1" />   */}
      <ScrollToTopButton /> 
    </div>
  );
};

export default InputOutput;