import React from 'react';
import ScrollToTopButton from '../components/ScrollTopButton';

const Functions = () => {
  return (
    <div className="lesson-container">
      <header className="lesson-header">
        <h1></h1>
      </header>

      <section className="lesson-section">
        <h2>   </h2>
        <p>  Python     ,    .    
             <code>def</code>,        . :</p>
        <pre>
          <code>
            {`def greet():\n`}
            {`print("Hello, world!")\n`}
          </code>
        </pre>
        <p>  ,   ,     :</p>
        <pre>
          <code>
            {`greet()\n`}
            {`# : Hello, world!`}
          </code>
        </pre>
      </section>

      <section className="lesson-section">
        <h2>  </h2>
        <p>   ,     .      
            . :</p>
        <pre>
          <code>
            {`def greet(name):\n`}
            {`print(f"Hello, {name}!")\n\n`}

            {`greet("Alice")\n`}
            {`# : Hello, Alice!\n`}
          </code>
        </pre>
        <p>       .    ,    
          :</p>
        <pre>
          <code>
            {`def greet(name="World"):\n`}
            {`print(f"Hello, {name}!")\n\n`}

            {`greet()\n`}
            {`# : Hello, World!\n`}
            {`greet("Bob")\n`}
            {`# : Hello, Bob!\n`}
          </code>
        </pre>
      </section>

      <section className="lesson-section">
        <h2> </h2>
        <p>       <code>return</code>.      
            . :</p>
        <pre>
          <code>
            {`def add(a, b):\n`}
            {`return a + b\n\n`}

            {`result = add(3, 5)\n`}
            {`print(result)\n`}
            {`# : 8\n`}
          </code>
        </pre>
        <p>  <code>return</code>    ,        
        :</p>
        <pre>
          <code>
            {`def early_return():\n`}
            {`return "Done"\n`}
            {`print("This will not be printed")\n\n`}

            {`print(early_return())\n`}
            {`# : Done\n`}
          </code>
        </pre>
      </section>

      <section className="lesson-section">
        <h2>  </h2>
        <p>,   ,    .       . 
          :</p>
        <pre>
          <code>
            {`def my_function():\n`}
            {`local_var = "I'm local"\n`}
            {`print(local_var)\n\n`}
                        
            {`global_var = "I'm global"\n`}
            {`my_function()\n`}
            {`print(global_var)\n\n`}
                        
            {`#         \n`}
            {`# print(local_var)  # !\n`}
          </code>
        </pre>
        <p>,   ,        . ,  
                   <code>global</code>:</p>
        <pre>
          <code>
            {`global_var = "I'm global"\n`}

            {`def change_global():\n`}
            {`global global_var\n`}
            {`global_var = "I have been changed"\n\n`}

            {`change_global()\n`}
            {`print(global_var)\n`}
            {`# : I have been changed\n`}
          </code>
        </pre>
      </section>

      <section className="lesson-section">
        <h2></h2>
        <p>      Python,       
           .          :    , 
             ,      .    
                .       
               !</p>
      </section>
      <ScrollToTopButton /> 
    </div>
  );
};

export default Functions;
