import React from 'react';
import ScrollToTopButton from '../components/ScrollTopButton';

const Strings = () => {
  return (
    <div className="lesson-container">
      <header className="lesson-header">
        <h1></h1>
      </header>

      <section className="lesson-section">
        <h2></h2>
        <p>  Python    ,   . 
             , ,   .   Python   
                .</p>
      </section>

      <section className="lesson-section">
        <h2>  </h2>
        <p>  Python   ,    (  
             <code>+</code>),  (   ),   
             . :</p>
        <pre>
          <code>
            {`str1 = "Hello"\n`}
            {`str2 = "World"\n`}
            {`concatenated = str1 + " " + str2\n`}
            {`print(concatenated)  # : Hello World\n\n`}

            {`repeated = str1 * 3\n`}
            {`print(repeated)  # : HelloHelloHello\n\n`}

            {`first_char = str1[0]\n`}
            {`print(first_char)  # : H\n`}
          </code>
        </pre>
        <p>    ,       
            Python.   ,   Python  .    
                .      
              . </p>
        <p>    <code>string[start:stop:step]</code>, :</p>
        <ul>
          <li><code>start</code> -    (  0).</li>
          <li><code>stop</code> -    (    ).</li>
          <li><code>step</code> -   (  1).</li>
        </ul>
        <p>  :</p>
        <pre>
          <code>
            {`str = "Hello, World!"\n\n`}

            {`#   \n`}
            {`substring = str[7:12]\n`}
            {`print(substring)  # : World\n\n`}

            {`#     " "\n`}
            {`#       ,    \n`}
            {`#   - ,   start =  , end = 0\n`}
            {`reversed_str = str[::-1] \n`}
            {`print(reversed_str)  # : !dlroW ,olleH\n`}
          </code>
        </pre>
      </section>

      <section className="lesson-section">
        <h2> </h2>
        <p>   Python     ,  
            <code>%</code>,  <code>.format()</code>  f- 
          ( ).          
            . :</p>
        <pre>
          <code>
            {`name = "Alice"\n`}
            {`age = 30\n`}
            {`formatted_str = "Name: %s, Age: %d" % (name, age)\n`}
            {`print(formatted_str)  # : Name: Alice, Age: 30\n\n`}

            {`formatted_str2 = "Name: {}, Age: {}".format(name, age)\n`}
            {`print(formatted_str2)  # : Name: Alice, Age: 30\n\n`}

            {`formatted_str3 = f"Name: {name}, Age: {age}"\n`}
            {`print(formatted_str3)  # : Name: Alice, Age: 30\n`}
          </code>
        </pre>
        <p>   Python        
             .</p>
      </section>

      <section className="lesson-section">
        <h2> </h2>
        <p>  Python    ,    . 
                <code>upper()</code>    
           , <code>lower()</code>     :</p>
        <pre>
          <code>
            {`sentence = "Hello, world!"\n`}
            {`print(sentence.upper())  # : HELLO, WORLD!\n`}
            {`print(sentence.lower())  # : hello, world!\n`}
          </code>
        </pre>
        <p>      :</p>
        <ul>
          <li><code>strip()</code> -       .</li>
          <li><code>replace(old, new)</code> -     <code>old</code>  <code>new</code>.</li>
          <li><code>find(sub)</code> -     <code>sub</code>      .</li>
          <li><code>count(sub)</code> -     <code>sub</code>  .</li>
          <li><code>startswith(prefix)</code> - ,       <code>prefix</code>.</li>
        </ul>
      </section>

      <section className="lesson-section">
        <h2></h2>
        <p>      Python,     
           .        ,  
                .      
                  . 
               ,    !</p>
      </section>
      <ScrollToTopButton /> 
    </div>
  );
};

export default Strings;
