import React from 'react';
import ScrollToTopButton from '../components/ScrollTopButton';

const AdvancedOOP = () => {
  return (
    <div className="lesson-container">
      <div className="lesson-header">
        <h1> </h1>
      </div>

      <div className="lesson-section">
        <h2>   </h2>
        <p> -  ()  Python    
          ,   .       
            ,       .</p>
      </div>

      <div className="lesson-section">
        <h2></h2>
        <p>  Python        . 
                  ,   
               .</p>
        <p>   Python:</p>
        <pre>
          <code>
            {`class Animal:\n`}
            {`    def speak(self):\n`}
            {`        raise NotImplementedError("Subclass must implement abstract method")\n\n`}

            {`class Dog(Animal):\n`}
            {`    def speak(self):\n`}
            {`        return "Woof!"\n\n`}

            {`class Cat(Animal):\n`}
            {`    def speak(self):\n`}
            {`        return "Meow!"\n\n`}

            {`dog = Dog()\n`}
            {`print(dog.speak())  # : Woof!\n`}

            {`cat = Cat()\n`}
            {`print(cat.speak())  # : Meow!\n`}
          </code>
        </pre>
      </div>

      <div className="lesson-section">
        <h2></h2>
        <p>  Python            . 
                ,    ,     
           .</p>
        <p>   Python:</p>
        <pre>
          <code>
            {`class Account:\n`}
            {`    def __init__(self, owner, balance=0):\n`}
            {`        self.owner = owner\n`}
            {`        self.__balance = balance\n\n`}

            {`    def deposit(self, amount):\n`}
            {`        if amount > 0:\n`}
            {`            self.__balance += amount\n\n`}

            {`    def withdraw(self, amount):\n`}
            {`        if 0 < amount <= self.__balance:\n`}
            {`            self.__balance -= amount\n\n`}

            {`    def get_balance(self):\n`}
            {`        return self.__balance\n\n`}

            {`acc = Account("John")\n`}
            {`acc.deposit(1000)\n`}
            {`acc.withdraw(500)\n`}
            {`print("Balance: $" + str(acc.get_balance()))  # : Balance: $500\n`}
          </code>
        </pre>
      </div>

      <div className="lesson-section">
        <h2></h2>
        <p>  Python        . 
                    
               -.</p>
        <p>   Python:</p>
        <pre>
          <code>
            {`class Shape:\n`}
            {`    def area(self):\n`}
            {`        raise NotImplementedError("Subclass must implement abstract method")\n\n`}

            {`class Rectangle(Shape):\n`}
            {`    def __init__(self, width, height):\n`}
            {`        self.width = width\n`}
            {`        self.height = height\n\n`}

            {`    def area(self):\n`}
            {`        return self.width * self.height\n\n`}

            {`class Circle(Shape):\n`}
            {`    def __init__(self, radius):\n`}
            {`        self.radius = radius\n\n`}

            {`    def area(self):\n`}
            {`        return 3.14 * self.radius * self.radius\n\n`}

            {`def print_area(shape):\n`}
            {`    print(f"Area: {shape.area()}")\n\n`}

            {`rectangle = Rectangle(5, 10)\n`}
            {`circle = Circle(7)\n\n`}

            {`print_area(rectangle)  # : Area: 50\n`}
            {`print_area(circle)     # : Area: 153.86\n`}
          </code>
        </pre>
        <p>      Shape,    area,      . 
                   ,     ,  
                  area.</p>
      </div>

      <div className="lesson-section">
        <h2></h2>
        <p>     -   Python. 
           ,        
            ,         
           .</p>
      </div>
      <ScrollToTopButton /> 
    </div>
  );
};

export default AdvancedOOP;
